.footer {
  margin-top: 50px;
  background-color: #f0f0f0; }
  .footer .bloques {
    display: flex;
    padding: 40px; }
    .footer .bloques a:hover {
      color: #E91E63; }
    .footer .bloques .bloque {
      font-size: 14px;
      flex: 1; }
      .footer .bloques .bloque .title {
        font-size: 18px;
        font-weight: 600; }
  .footer .copy {
    width: 100%;
    text-align: center;
    margin-bottom: 15px;
    position: relative;
    display: inline-block; }

@media (max-width: 769px) {
  .footer .bloques {
    flex-direction: column; } }
