@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:400,700&display=swap");
@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
body {
  font-size: 15px;
  font-family: 'Roboto Slab', serif;
  line-height: 1.80857;
  margin: 0; }

a {
  color: #1f1f1f;
  text-decoration: none !important;
  outline: none !important;
  -webkit-transition: all .3s ease-in-out;
  -moz-transition: all .3s ease-in-out;
  -ms-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out; }

.totop {
  display: block;
  position: fixed;
  width: 40px;
  height: 40px;
  bottom: 40px;
  right: 40px;
  background-color: black;
  opacity: 0.5;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  border-radius: 4px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s; }

.totop:before {
  content: "\f102";
  height: 20px;
  width: 20px;
  position: relative; }

.totop.show {
  display: block; }

.totop.hidden {
  display: none; }

.bold {
  font-weight: bold; }

h1 a:hover, h2 a:hover, h3 a:hover, h4 a:hover, h5 a:hover, h6 a:hover {
  opacity: .8; }

a {
  color: #1f1f1f;
  text-decoration: none;
  outline: none; }

a, .btn {
  text-decoration: none !important;
  outline: none !important;
  -webkit-transition: all .3s ease-in-out;
  -moz-transition: all .3s ease-in-out;
  -ms-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out; }

.btn-custom {
  margin-top: 20px;
  background-color: transparent !important;
  border: 2px solid #ddd;
  padding: 12px 40px;
  font-size: 16px; }

.lead {
  font-size: 18px;
  line-height: 30px;
  color: #767676;
  margin: 0;
  padding: 0; }

blockquote {
  margin: 20px 0 20px;
  padding: 30px; }

ul, li, ol {
  list-style: none;
  margin: 0px;
  padding: 0px; }

button:focus {
  outline: none;
  box-shadow: none; }

:focus {
  outline: 0; }

p {
  margin: 0px; }

.bootstrap-select .dropdown-toggle:focus {
  outline: none !important; }

.form-control::-moz-placeholder {
  color: #ffffff;
  opacity: 1; }

.bootstrap-select .dropdown-toggle:focus {
  box-shadow: none !important; }

.row {
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px; }

h1 {
  font-size: 2.75rem; }

h2 {
  font-size: 2.25rem; }

h3 {
  font-size: 1.75rem; }

.loading {
  position: absolute;
  top: 50%;
  left: 43%; }

.container-categories {
  margin-top: 50px; }

.categoria-ampliado {
  display: block;
  width: 90%;
  text-align: left;
  display: block;
  text-align: left;
  margin: auto; }
