.headerFirst {
  display: flex;
  padding-top: .5em;
  padding-bottom: .5em;
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  width: 100%;
  top: 0;
  left: 0;
  position: fixed;
  z-index: 1100;
  box-shadow: 0 1px 5px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0 1px 5px 0px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0 1px 5px 0px rgba(0, 0, 0, 0.2);
  -o-box-shadow: 0 1px 5px 0px rgba(0, 0, 0, 0.2);
  -ms-box-shadow: 0 1px 5px 0px rgba(0, 0, 0, 0.2);
  /* ================================= 
  Media Queries
==================================== */
  /* Landscape phone to portrait tablet */ }
  .headerFirst ul {
    margin: 0;
    padding: 0;
    list-style: none;
    padding-right: 35px; }
  .headerFirst .logo img {
    height: 55px;
    vertical-align: middle;
    font-family: 'Roboto Slab', serif; }
  .headerFirst h2,
  .headerFirst h3,
  .headerFirst a {
    color: #34495e; }
  .headerFirst a {
    text-decoration: none; }
  .headerFirst .logo {
    margin: 0;
    font-size: 1.45em; }
  .headerFirst .mainnav {
    margin-top: 5px; }
  .headerFirst .logo a,
  .headerFirst .mainnav a {
    padding: 10px 15px;
    text-transform: uppercase;
    text-align: center;
    display: block; }
  .headerFirst .mainnav a {
    color: #34495e;
    font-size: .99em; }
  .headerFirst .mainnav a:hover {
    color: #E91E63; }
  @media (max-width: 767px) {
    .headerFirst .logo img {
      display: none; } }
  @media (min-width: 769px) {
    .headerFirst .headerFirst,
    .headerFirst .mainnav {
      display: flex; }
    .headerFirst .headerFirst {
      flex-direction: column;
      align-items: center; }
      .headerFirst .headerFirst .headerFirst {
        width: 80%;
        margin: 0 auto;
        max-width: 1150px; } }
  @media (min-width: 1025px) {
    .headerFirst .headerFirst {
      flex-direction: row;
      justify-content: space-between; } }
